const Sprites = {
	spritesheet: null,
};

const LoadSpriteSheet = async ( spritesheet_file ) => {
	return new Promise( (resolve, reject) => {
		const img = new Image();
		img.onload = () => { resolve( img ); };
		img.onerror = () => { reject( 'Image loading fail' ) }
		img.src = spritesheet_file;
	});
}

